.services{
    background-image: url('../../Assets/Logo/Theme.jpg');
    background-Position: center;
    background-Size: cover;
    background-Repeat: no-repeat;
}
.services__content0{
    display: flex;
    justify-content: space-evenly;
    border-radius: 15px 15px 0px 0px;
    padding:30px;
    padding-top:30px;
    width: 100%;
    /* margin-top: 250px;  */
    border-top: solid 4px #f7cf36;
    Font-size: 50px;
    Font-weight:bold;
    text-align: center !important;
}

.services__content1{
    display: flex;
    justify-content: space-evenly;
    padding:30px;
    padding-top:10px;
    width: 100%;
}
.services__content2{
    display: flex;
    justify-content: space-evenly;
    padding:30px;
    padding-top:20px;
    width: 100%;
    margin-top: 0px; 
}
.services__content__image{
    outline: none;
    border: none;
}
.services__content__image > img{
    height: 120px;
    outline: none;
    border: none;
}

/* .services__h3{
padding-top:5px;
text-align: center;
color: black;
outline: none;
border: none;
